
html {
  overflow-y: scroll;
  -webkit-text-size-adjust: none;
  -webkit-user-select: none;
  -webkit-text-size-adjust: 100%;
  margin: 0 auto !important;
  min-width: 320px;
  max-width: 720px;
}
html * {
    -webkit-tap-highlight-color: transparent;
    -webkit-tap-highlight-color: transparent;
}
html, body {
  font-family: sans-serif;
  color: #999;
}
body, div, dl, dt, dd, ul, ol, li, h1, h2, h3, h4, h5, h6, pre, code, form, fieldset, legend, input, textarea, p, blockquote, th, td, hr, button, article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section {
  margin: 0;
  padding: 0;
  font-weight: normal;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
caption, th {
  text-align: left;
}
fieldset, img {
  border: 0;
}
em, i, strong {
  font-style: normal;
}
h1, h2, h3, h4, h5, h6 {
  font-size: 100%;
  font-weight: normal;
}
ul, ol {
  list-style: none;
}
del {
  text-decoration: line-through;
}
input, button, textarea, select {
  font-size: 100%;
  border: none;
  background: none;
  -webkit-appearance: none;
}
a, button, input, select, textarea, a:focus, button:focus, input:focus, select:focus, textarea:focus {
  outline: 0 none;
}
a {
  text-decoration: none;
  color: #666;
}
.clearfix:before, .clearfix:after {
  content: "";
  display: table;
}
.clearfix:after {
  clear: both;
}
::-webkit-input-placeholder {
  color: #999;
}
::-moz-placeholder {
  color: #999;
}
:-moz-placeholder {
  color: #999;
}
@media only screen and (max-width: 359px) {
html {
    font-size: 12px;
}
}
@media only screen and (min-width: 360px) and (max-width: 374px) {
html {
    font-size: 13.5px;
}
}
@media only screen and (min-width: 375px) and (max-width: 399px) {
html {
    font-size: 14.0625px;
}
}
@media only screen and (min-width: 400px) and (max-width: 479px) {
html {
    font-size: 15px;
}
}
@media only screen and (min-width: 480px) and (max-width: 539px) {
html {
    font-size: 18px;
}
}
@media only screen and (min-width: 540px) and (max-width: 639px) {
html {
    font-size: 20.25px;
}
}
@media only screen and (min-width: 640px) and (max-width: 719px) {
html {
    font-size: 24px;
}
}
@media only screen and (min-width: 720px) {
html {
    font-size: 27px;
}
}
body {
  background-image: -webkit-gradient(linear, left bottom, left top, from(#eeeeee), to(#f9f9f9));
  background-image: linear-gradient(to top, #eeeeee, #f9f9f9);
}



