
.mp-wrapper {
  width: 100%;
}
.mp-wrapper:after {
    content: '';
    position: absolute;
    width: 8.96296rem;
    height: 13.18519rem;
    right: 0;
    top: 1.55556rem;
    background: url(../assets/combined-shape.svg);
    background-size: 100% 100%;
}
.mp-wrapper:before {
    content: '';
    position: absolute;
    width: 9.48148rem;
    height: 9.48148rem;
    left: -1.77778rem;
    top: -2.96296rem;
    border-radius: 100%;
    background-image: -webkit-gradient(linear, left bottom, left top, from(#f0f0f0), to(#f5f5f5));
    background-image: linear-gradient(to top, #f0f0f0, #f5f5f5);
}
.mp-logo {
  width: 7.62963rem;
  height: 2.37037rem;
  margin: 1.77778rem auto 0;
}
.mp-logo img {
    width: 100%;
    height: auto;
}
.mp-pic {
  width: 13.48148rem;
  height: 9.03704rem;
  margin: 2.96296rem auto 0;
  position: relative;
}
.mp-pic img {
    width: 100%;
    height: auto;
}
.mp-pic:before {
    content: '';
    position: absolute;
    width: 4.14815rem;
    height: 4.14815rem;
    border-radius: 100%;
    left: -5.62963rem;
    bottom: -1.48148rem;
    background-image: -webkit-gradient(linear, left bottom, left top, from(#fff), color-stop(rgba(255, 255, 255, 0.4)), to(rgba(255, 255, 255, 0.2)));
    background-image: linear-gradient(to top, #fff, rgba(255, 255, 255, 0.4), rgba(255, 255, 255, 0.2));
}
.mp-subtitle {
  text-align: center;
  margin-top: 1.62963rem;
}
.mp-subtitle span {
    display: inline-block;
    color: #888;
    font-size: 0.88889rem;
    line-height: 1.18519rem;
    font-weight: normal;
    position: relative;
}
.mp-subtitle span:before, .mp-subtitle span:after {
      content: '';
      position: absolute;
      width: 2.96296rem;
      height: 0.11111rem;
      top: 0.55556rem;
      background-color: #e1e1e1;
}
.mp-subtitle span:before {
      left: -3.55556rem;
}
.mp-subtitle span:after {
      right: -3.55556rem;
}
.mp-subtitle .subdet {
    color: #888;
    margin-top: 0.44444rem;
}
.mp-subtitle .subdet strong {
      color: #23232b;
      margin-left: 0.2963rem;
}
.mp-info {
  width: 100%;
}
.mp-info ul {
    width: 100%;
    clear: both;
    margin-top: 2.96296rem;
}
.mp-info ul li {
      width: 13.85185rem;
      padding: 0 2.37037rem 0 7.7037rem;
      height: 9.18519rem;
      margin: 0 auto 1.18519rem;
      border-radius: 0.22222rem;
      background-color: #fff;
      position: relative;
      display: table;
}
.mp-info ul li .mp-Cnt {
        display: table-cell;
        vertical-align: middle;
}
.mp-info ul li .mp-Cnt .mp-infoTitle {
          line-height: 2.37037rem;
          font-size: 1.48148rem;
          color: #23232b;
}
.mp-info ul li i {
        display: inline-block;
        width: 3.55556rem;
        height: 3.55556rem;
        position: absolute;
        left: 2.37037rem;
        top: 2.81481rem;
}
.mp-info ul li .icon-net {
        background-image: url("../assets/networking.svg");
        background-size: 100% 100%;
}
.mp-info ul li .icon-laptop {
        background-image: url("../assets/laptop.svg");
        background-size: 100% 100%;
}
.mp-info ul li .icon-mbag {
        background-image: url("../assets/money-bag.svg");
        background-size: 100% 100%;
}
.mp-info ul li .icon-present {
        background-image: url("../assets/presentation.svg");
        background-size: 100% 100%;
}



